/**
 * Home SCSS
 */

.slider-wrap {
  .items {
    .item {
      padding: 20px 10px 0;
      &:focus {
        outline: none;
      }
      img {
        border-radius: 4px;
      }
    }
  }
  .slick-dots {
    position: absolute;
    bottom: 30px;
    left: 0;
    right: 0;
    margin: 0 auto;
    padding: 0;
    text-align: center;
    @media (max-width: 992px) {
      bottom: 10px;
    }
    li {
      display: inline-block;
      button {
        height: 13px;
        width: 13px;
        margin: 0 3px;
        border: none;
        border-radius: 50%;
        background: #D2D2D2;
        font-size: 0;
        vertical-align: middle;
        @include css3-prefix(transition, all .5s ease-in-out);
        cursor: pointer;
        box-shadow: none;
        @media (max-width: 992px) {
          height: 2px;
          border-radius: 0;
        }
      }
      &.slick-active {
        button {
          border-radius: 10px;
          width: 30px;
          background-color: $base-color;
        }
      }
    }
  }
}
.top-products,
.sponsored {
  .product-grids {
    @include css3-prefix(display, flex);
    @include css3-prefix(flex-wrap, wrap);
    .grid {
      @include css3-sufix(width, calc(16.66% - 25px));
      margin-right: 30px;
      margin-bottom: 30px;
      &:nth-child(6n) {
        margin-right: 0;
      }
      @media (max-width: 1366px) {
        @include css3-sufix(width, calc(16.66% - 16.66px));
        margin-bottom: 20px;
        margin-right: 20px;
      }
      @media (max-width: 1200px) {
        @include css3-sufix(width, calc(20% - 16px));
        &:nth-child(6n) {
          margin-right: 20px;
        }
        &:nth-child(5n) {
          margin-right: 0;
        }
      }
      @media (max-width: 992px) {
        @include css3-sufix(width, calc(25% - 15px));
        &:nth-child(5n) {
          margin-right: 20px;
        }
        &:nth-child(4n) {
          margin-right: 0;
        }
      }
      @media (max-width: 767px) {
        @include css3-sufix(width, calc(33.33% - 13.5px));
        &:nth-child(4n) {
          margin-right: 20px;
        }
        &:nth-child(3n) {
          margin-right: 0;
        }
      }
      @media (max-width: 480px) {
        @include css3-sufix(width, calc(50% - 10px));
        &:nth-child(3n) {
          margin-right: 20px;
        }
        &:nth-child(2n) {
          margin-right: 0;
        }
      }
    }
  }
}
.product-grids {
  width: 100%;
  .grid {
    position: relative;
    padding: 10px;
    border: 1px solid #E8E7EA;
    border-radius: 4px;
    text-align: center;
    @include css3-prefix('transition' , 'all .3s linear');
    &:hover {
      border-color: $base-color;
      @include css3-prefix('transition' , 'all .3s linear');
      .product-img {
        img {
          @include css3-prefix('transform' , 'scale3d(1, 1, 1)');
          @include css3-prefix('transition' , 'all .5s linear');
        }
      }
      .quickview {
        top: 50%;
        visibility: visible;
        opacity: 1;
        @include css3-prefix('transition' , 'all .5s linear');
      }
    }
    .product-img {
      margin-bottom: 15px;
      img {
        @include css3-prefix('transform' , 'scale3d(.8, .8, .8)');
        @include css3-prefix('transition' , 'all .5s linear');
      }
    }
    .product-text {
      .prod-cat {
        color: #999;
        font-size: 12px;
        font-weight: 400;
      }
      .prod-name {
        display: inline-block;
        width: 100%;
        min-height: 62px;
        padding: 10px 0;
        a {
          color: #333;
          font-size: 12px;
          font-weight: 400;
          &:hover {
            color: $base-color;
          }
        }
      }
      .prod-price {
        display: inline-block;
        width: 100%;
        font-size: 12px;
        color: #333;
        font-weight: 600;
        .product-old-price {
          color: #999;
          text-decoration: line-through;
        }
        .product_off {
          color: $base-color;
          font-weight: 300;
        }
      }
    }
  }
}

.sponsored {
  display: inline-block;
  width: 100%;
  padding: 38px 0;
  @media (max-width: 767px) {
    padding: 10px 0;
  }
}

.top-products {
  display: inline-block;
  width: 100%;
  padding-bottom: 60px;
  @media (max-width: 992px) {
    padding-bottom: 10px;
  }
}

.popular-product {
  display: inline-block;
  width: 100%;
  padding: 60px 0;
  background-color: #F8F8F8;
  @media (max-width: 992px) {
    padding: 20px 0 0;
  }
  h2 {
    text-align: center;
  }
  .product-tabs {
    .tab-list {
      display: inline-block;
      width: 100%;
      text-align: center;
      @media (max-width: 992px) {
        display: none;
      }
      .nav-tabs {
        margin: 0 auto;
        margin-bottom: 20px;
        padding-bottom: 12px;
        border-bottom: none;
        overflow-x: auto;
        overflow-y: hidden;
        text-align: center;
        @include css3-prefix('flex-wrap' , 'nowrap');
        @include css3-sufix('display' , 'inline-flex');
        li {
          position: relative;
          display: inline-block;
          margin-right: 40px;
          font-size: 15px;
          font-weight: 400;
          color: $primary-color;
          a {
            display: inline-block;
            padding: 10px 0;
            color: inherit;
            vertical-align: top;
            &:hover,
            &.active {
              color: $base-color;
              &::before {
                width: 100%;
                @include css3-prefix('transition' , ' all .3s ease-out 0s');
              }
            }
            &::before {
              position: absolute;
              bottom: 0;
              left: 0;
              height: 1px;
              width: 0;
              background-color: $base-color;
              content: '';
              @include css3-prefix('transition' , ' all .3s ease-out 0s');
            }
          }
        }
      }
    }
    .tab-content {
      >.tab-pane {
        @media (max-width: 992px) {
          display: inline-block;
          width: 100%;
          margin-bottom: 20px;
        }
      }
      .product-tile {
        display: grid;
        grid-gap: 1px;
        grid-template-columns: repeat(5, 1fr);
        border: solid 1px #DCDCDC;
        border-radius: 4px;
        background-color: #DCDCDC;
        @media (max-width: 767px) {
          grid-template-columns: auto;
        }
        .grid {
          position: relative;
          background-color: $white;
          &.grid-1 {
            grid-column-start: 1;
            grid-column-end: 3;
            grid-row-start: 1;
            grid-row-end: 3;
          }
          @media (min-width: 768px) {
            &:hover {
              .product-img {
                img {
                  @include css3-prefix('transform' , 'scale3d(.9, .9, .9)');
                }
              }
              .content-overlay {
                opacity: 1;
              }
              .content-details {
                top: 50%;
                left: 50%;
                opacity: 1;
              }
            }
          }
          .product-img {
            position: relative;
            display: block;
            height: 100%;
            width: 100%;
            padding: 0 0 100%;
            overflow: hidden;
            img {
              position: absolute;
              max-width: 100%;
              width: 100%;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              margin: auto;
              @include css3-prefix('transition' , 'all .5s linear');
              @include css3-prefix('transform' , 'scale3d(.8, .8, .8)');
            }
          }
          .content-overlay {
            position: absolute;
            height: 100%;
            width: 100%;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 1;
            background: rgba(0, 0, 0, .7);
            opacity: 0;
            pointer-events: none;
            @include css3-prefix('transition' , 'all 0.4s ease-in-out 0s');
          }
          .content-details {
            position: absolute;
            top: 80%;
            left: 50%;
            z-index: 1;
            width: 100%;
            padding: 15px 0;
            text-align: center;
            opacity: 0;
            @include css3-prefix('transform' , 'translate(-50%, -50%)');
            @include css3-prefix('transition' , 'all .3s ease-in-out 0s');
            .prod-price {
              display: inline-block;
              width: 100%;
              font-size: 12px;
              color: #333;
              font-weight: 600;
              .product-old-price {
                color: #999;
                text-decoration: line-through;
              }
              .product_off {
                color: $base-color;
                font-weight: 300;
              }
            }
            .product-title a {
              &:hover {
                color: $base-color;
              }
            }
            .product-title a,
            .prod-price {
              padding: 5px 0;
              color: $white;
            }
          }
        }
      }
    }
  }
}

.top-collections {
  @media (max-width: 992px) {
    display: none;
  }
  .owl-nav {
    @include css3-sufix(top, calc(50% - 21px));
    .owl-prev {
      float: left;
      margin-left: -21px;
    }
    .owl-next {
      float: right;
      margin-right: -21px;
    }
    .owl-prev,
    .owl-next {
      background-color: $white;
      background-image: url('../images/back-black.svg');
    }
  }
}
.categories-wrap {
  display: inline-block;
  width: 100%;
  padding: 20px 15px 0;
  @media (min-width: 768px) {
    display: none !important;
  }
  ul {
    @include css3-sufix(display, flex);
    @include css3-prefix(flex-wrap, wrap);
    @include css3-prefix(justify-content, space-between);
    li {
      width: 20%;
      margin-bottom: 10px;
      a {
        display: inline-block;
        width: 100%;
        text-align: center;
        .icon {
          display: inline-block;
          height: 50px;
          width: 50px;
          border-radius: 100%;
          background-color: $base-color;
          line-height: 50px;
          img {
            max-width: 30px;
          }
        }
        span {
          display: inline-block;
          width: 100%;
          padding: 0 5px;
          overflow: hidden;
          color: #333;
          font-size: 11px;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .tabs-con {
    display: none;
    &.active {
      display: block;
    }
  }
}
@media (min-width: 768px) {
  #categories,
  #cart,
  #account {
    display: none !important;
  }
}
.mobile-tabs-wrap {
  position: fixed;
  top: auto;
  bottom: 0;
  left: 0;
  z-index: 999;
  display: inline-block;
  height: 54px;
  width: 100%;
  padding: 0 12px;
  background-color: $white;
  @include css3-prefix(box-shadow, 0 1px 3px 0);
  @media (min-width: 768px) {
    display: none !important;
  }
  ul {
    height: 100%;
    @include css3-sufix(display, flex);
    @include css3-prefix(justify-content, space-between);
    @include css3-prefix(align-items, center);
    li {
      width: 25%;
      text-align: center;
      a {
        @include css3-sufix(display, flex);
        @include css3-sufix(flex-wrap, wrap);
        @include css3-prefix(justify-content, center);
        color: #3A3E4A;
        &:hover,
        &.active {
          color: $base-color;
        }
        span {
          display: inline-block;
          width: 100%;
          font-size: 12px;
        }
      }
    }
  }
}

