/**
 * Footer SCSS
 */

footer {
  display: inline-block;
  width: 100%;
  padding-top: 60px;
  border-top: 1px solid #DDD;
  @media (max-width: 767px) {
    display: none;
  }
  .footer-group {
    .footer-title {
      margin-bottom: 20px;
      color: $primary-color;
      font-size: 16px;
      font-weight: 500;
      font-family: 'Poppins', sans-serif;
    }
    .footer-links {
      li {
        padding: 5px 0;
        a {
          position: relative;
          display: inline-block;
          color: $primary-color;
          font-size: 14px;
          font-weight: 300;
          @include css3-prefix('transition' , ' all .3s ease');
          &::before {
            position: absolute;
            bottom: 0;
            left: 0;
            height: 1px;
            width: 0;
            background-color: $base-color;
            content: '';
            @include css3-prefix('transition' , ' all .3s ease-out 0s');
          }
          &:hover {
            margin-left: 10px;
            color: $base-color;
            &::before {
              width: 100%;
              @include css3-prefix('transition' , ' all .3s ease-out 0s');
            }
          }
        }
      }
    }
    .store-button {
      a {
        display: inline-block;
        &:hover {
          color: white;
        }
        img {
          height: 15px;
          width: 15px;
          margin-right: 5px;
        }
      }
    }
    p {
      font-size: 14px;
    }
    .newsletter {
      @include css3-sufix('display' , 'flex');
      .form-item {
        display: inline-block;
        width: 100%;
      }
      .form-action {
        input {
          width: 136px;
          border-radius: 0 2px 2px 0;
          font-size: 16px;
          font-weight: 300;
        }
      }
    }
    .contact-info {
      width: 100%;
      margin-top: 15px;
      @include css3-sufix('display' , 'inline-flex');
      @include css3-prefix('align-items' , 'center');
      li {
        margin: 10px 15px 10px 0;
        font-size: 14px;
        font-weight: 300;
        @include css3-sufix('display' , 'inline-flex');
        @include css3-prefix('align-items' , 'center');
        a {
          color: $primary-color;
        }
        img {
          max-height: 15px;
          max-width: 15px;
          margin-right: 6px;
        }
      }
    }
  }
  .bottom-footer {
    margin-top: 40px;
    padding: 40px 0;
    border-top: solid 1px #E8E7EA;
    p {
      margin-bottom: 0;
      font-size: 14px;
    }
  }
}

.featured-product {
  display: inline-block;
  width: 100%;
  padding: 60px 0;
  @media (max-width: 992px) {
    padding: 20px 0 10px;
  }
  .banner-wrap {
    display: inline-block;
    width: 100%;
    margin-bottom: 60px;
    @media (max-width: 992px) {
      margin-bottom: 20px;
    }
  }
  h2 {
    text-align: left;
  }
  .product-horizontal {
    .horizontal-product {
      position: relative;
      width: 100%;
      margin-bottom: 30px;
      border: solid 1px #DCDCDC;
      border-radius: 4px;
      background: $white;
      overflow: hidden;
      @include css3-sufix('display' , 'flex');
      @include css3-prefix('transition' , 'all .2s linear');
      @media (max-width: 992px) {
        margin-bottom: 20px;
      }
      &:hover {
        border-color: $base-color;
        @include css3-prefix('transition' , 'all .3s linear');
        .quickview {
          top: 50%;
          visibility: visible;
          opacity: 1;
          @include css3-prefix('transition' , 'all .5s linear');
        }
        .product-img {
          img {
            @include css3-prefix('transform' , 'scale3d(.9, .9, .9)');
            @include css3-prefix('transition' , 'all .5s linear');
          }
        }
      }
      .product-img {
        position: relative;
        display: inline-block;
        max-width: 150px;
        width: 100%;
        overflow: hidden;
        img {
          @include css3-prefix('transition' , 'all .5s linear');
          @include css3-sufix('transform' , 'scale3d(.8, .8, .8)');
        }
      }
      .product-text {
        padding: 20px;
        text-align: left;
        @include css3-sufix('width' , 'calc(100% - 150px)');
        .prod-cat {
          color: #999;
          font-size: 13px;
          font-weight: 400;
        }
        .prod-name {
          min-height: 62px;
          margin: 0 auto;
          padding: 10px 10px 10px 0;
          a {
            color: #333;
            font-size: 14px;
            font-weight: 400;
            &:hover {
              color: $base-color;
            }
          }
        }
        .prod-price {
          display: inline-block;
          width: 100%;
          font-size: 13px;
          color: #333;
          font-weight: 600;
          .product-old-price {
            color: #999;
            text-decoration: line-through;
          }
          .product_off {
            display: inline-block;
            color: $base-color;
            font-weight: 300;
          }
        }
      }
    }
  }
  .banner-fp {
    margin-top: 60px;
    @media (max-width: 992px) {
      margin-top: 0;
    }
    .banner-image {
      @media (max-width: 992px) {
        margin-bottom: 10px;
      }
      img {
        border-radius: 4px;
      }
    }
  }
}

.top-collections {
  display: inline-block;
  width: 100%;
  padding: 60px 0;
  background: #6DCDEF;
  h2 {
    color: $white;
    .view-more {
      float: right;
      display: inline-block;
      a {
        color: $white;
        font-size: 16px;
        text-decoration: underline;
        &:hover {
          color: $base-color;
        }
      }
    }
  }
  .collection-slider {
    .grid {
      background-color: $white;
      &:hover {
        border-color: #E8E7EA;
      }
    }
  }
}

.top-categories {
  display: inline-block;
  width: 100%;
  padding: 60px 0;
  background-color: #F8F8F8;
  @media (max-width: 992px) {
    display: none;
  }
  h2 {
    text-align: left;
  }
  .top-bundle {
    min-height: 292px;
    height: 100%;
    overflow: hidden;
    margin-bottom: 20px;
    border-radius: 0 0 4px 4px;
    background: $white;
    @include css3-prefix('box-shadow' , '0 2px 5px 0 rgba(0, 0, 0, .1)');
    .cat-img {
      position: relative;
      display: block;
      width: 100%;
      margin: 0 0 16px;
      padding: 0 0 25%;
      img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        max-width: 100%;
        margin: auto;
        @include css3-prefix('transition' , 'all .5s linear');
        @include css3-prefix('trnasform' , 'scale3d(1, 1, 1)');
      }
    }
    a {
      color: $primary-color;
      &:hover {
        color: $base-color;
      }
    }
    .cat-title {
      padding: 0 24px;
      font-size: 16px;
      font-weight: 500;
    }
    .cat-list {
      padding: 0 24px;
      li {
        display: block;
        margin: 10px 0;
        color: #515050;
        font-size: 14px;
      }
    }
  }
}

.top-brands {
  display: inline-block;
  width: 100%;
  padding: 60px 0;
  @media (max-width: 992px) {
    display: none;
  }
  h2 {
    text-align: center;
  }
  .view-more {
    display: inline-block;
    width: 100%;
    color: $base-color;
    text-align: center;
    text-decoration: underline;
    text-transform: capitalize;
  }
  .brand-img {
    @include css3-sufix('display' , 'flex');
    @include css3-prefix('justify-content' , 'space-around');
    li {
      width: 20%;
      a {
        height: 100px;
        margin: 0 auto;
        padding: 32px 10px;
        @include css3-sufix('display' , 'flex');
        @include css3-prefix('justify-content' , 'center');
        @include css3-prefix('align-items' , 'center');
      }
    }
  }
}
