/**
 * Header SCSS
 */

header {
  @include css3-prefix(transition, .7s all);
  @media (max-width: 992px) {
    display: none;
  }
  .top-header {
    float: left;
    display: inline-block;
    width: 100%;
    background-color: $base-color;
    color: $white;
    .left-wrap {
      float: left;
      display: inline-block;
      width: 50%;
      span {
        display: inline-block;
        padding: 8px 0;
        color: inherit;
        font-size: .775em;
      }
    }
    .right-wrap {
      float: right;
      display: inline-block;
      width: 50%;
      text-align: right;
      > div {
        position: relative;
        display: inline-block;
        margin: 8px 0;
        padding: 0 15px;
        border-left: 1px solid $white;
        color: $white;
        font-size: 12px;
        vertical-align: top;
        &:first-child {
          border-left: none;
        }
        .icon {
          display: inline-block;
          img {
            width: 17px;
            margin-right: 5px;
          }
        }
        .text {
          display: inline-block;
        }
      }
      .lang,
      .currency {
        position: relative;
        .icon {
          cursor: pointer;
        }
        .option-dropdown {
          position: absolute;
          top: 26px;
          left: 0;
          z-index: 9;
          display: inline-block;
          min-width: 200px;
          padding: 15px 25px;
          border-radius: 4px;
          background-color: $white;
          @include css3-prefix(box-shadow, 0 3px 4px 0 rgba($black, .2));
          @include css3-prefix(transform, scaleY(0));
          @include css3-prefix(transition, all 300ms);
          opacity: 0;
          visibility: hidden;
          &.show {
            @include css3-prefix(transform, scaleY(1));
            opacity: 1;
            visibility: visible;
          }
          &::before {
            position: absolute;
            top: -6px;
            left: 20px;
            display: block;
            border-bottom: 6px solid $white;
            border-left: 6px solid transparent;
            border-inline-end: 6px solid transparent;
            content: '';
          }
          ul {
            li {
              padding: 5px 0;
              color: #AEAEAF;
              font-size: 12px;
              font-weight: 600;
              text-align: left;
              text-transform: uppercase;
              a {
                position: relative;
                display: inline-block;
                padding: 5px 0;
                color: #333;
                font-size: 12px;
                font-weight: 400;
                text-align: left;
                &.is-active {
                  color: $base-color;
                  &::before {
                    position: absolute;
                    left: -15px;
                    top: calc(50% - 5px);
                    display: block;
                    height: 10px;
                    width: 10px;
                    background: url('../images/tick.svg') no-repeat 0 0;
                    background-size: 10px;
                    content: '';
                  }
                }
                &.active {
                  color: $base-color;
                }
              }
            }
          }
        }
      }
      .login-reg {
        a {
          color: $white;
        }
      }
    }
  }
  .middle-header {
    display: inline-block;
    width: 100%;
    padding: 20px;
    > div {
      @include css3-sufix(display, flex);
      @include css3-prefix(align-items, center);
    }
    .logo-wrap {
      max-height: 60px;
      width: 150px;
      margin-right: 25px;
      @include css3-sufix(display, inline-flex);
      @include css3-prefix(justify-content, center);
      @media (max-width: 992px) {
        width: 100px;
      }
      img {
        height: 45px;
        @media (max-width: 992px) {
          height: 30px;
        }
      }
    }
    .search-wrap {
      @include css3-sufix(width, calc(100% - 333px));
      @media (max-width: 992px) {
        @include css3-sufix(width, calc(100% - 245px));
      }
      form {
        @include css3-sufix(display, flex);
      }
      .select {
        position: relative;
        float: left;
        display: inline-block;
        height: 34px;
        border: 1px solid $base-color;
        border-right: none;
        border-radius: 3px 0 0 3px;
        background-color: transparent;
        select {
          display: none;
        }
        .select-styled {
          display: inline-block;
          width: max-content;
          padding: 0 35px 0 15px;
          color: #888;
          font-size: 12px;
          line-height: 34px;
          text-align: center;
          &::after {
            position: absolute;
            top: 50%;
            right: 14px;
            border-left: 5px solid transparent;
            border-right: 5px solid transparent;
            border-top: 5px solid $base-color;
            pointer-events: none;
            @include css3-prefix(transform, translateY(-50%));
            content: '';
          }
        }
        .select-options {
          position: absolute;
          top: 37px;
          left: 0;
          z-index: 9;
          display: inline-block !important;
          min-width: 200px;
          padding: 15px 25px;
          border-radius: 4px;
          background-color: $white;
          @include css3-prefix(box-shadow, 0 0 11px 0 rgba($black, .2));
          @include css3-prefix(transform, scaleY(0));
          @include css3-prefix(transition, all 300ms);
          opacity: 0;
          visibility: hidden;
          &::before {
            position: absolute;
            top: -6px;
            left: 20px;
            display: block;
            border-bottom: 6px solid $white;
            border-left: 6px solid transparent;
            border-inline-end: 6px solid transparent;
            content: '';
          }
          li {
            padding: 5px 0;
            color: #AEAEAF;
            font-size: 12px;
            font-weight: 600;
            text-align: left;
            text-transform: uppercase;
            color: #333;
            font-size: 12px;
            font-weight: 400;
            text-align: left;
            cursor: pointer;
            &:first-child {
              display: none;
            }
            &:nth-child(2) {
              color: #AEAEAF;
              font-size: 0.8em;
              font-weight: 600;
              text-align: left;
              text-transform: uppercase;
              pointer-events: none;
            }
            &.active {
              color: $base-color;
            }
          }
        }
        .select-styled.active + .select-options {
          @include css3-prefix(transform, scaleY(1));
          opacity: 1;
          visibility: visible;
        }
      }
      .field-item {
        float: left;
        display: inline-block;
        max-width: 100%;
        width: 100%;
        /*@include css3-sufix(width, calc(100% - 215px));*/
        input {
          height: 34px;
          width: 100%;
          padding: 2px 10px;
          border: 1px solid $base-color;
        }
      }
      .form-action {
        float: left;
        display: inline-block;
        min-width: 115px;
        .btn {
          height: 34px;
          width: 100%;
          padding: 0 10px;
          font-size: 15px;
          line-height: 34px;
          border-radius: 0 3px 3px 0;
          img {
            max-width: 22px;
          }
        }
      }
    }
    .cart-icon {
      display: inline-block;
      width: 150px;
      text-align: right;
      @media (max-width: 992px) {
        width: 120px;
      }
      .icon-wrap {
        position: relative;
        display: inline-block;
        height: 48px;
        width: 48px;
        border: 1px solid $base-color;
        border-radius: 100%;
        padding: 8px;
        vertical-align: middle;
        .count {
          position: absolute;
          bottom: 5px;
          left: 30px;
          z-index: 0;
          display: block;
          height: 22px;
          width: 22px;
          border-radius: 50%;
          background-color: $base-color;
          color: $white;
          font-size: 12px;
          line-height: 22px;
          text-align: center;
          vertical-align: middle;
        }
      }
      .text-amount {
        display: inline-block;
        @include css3-sufix(max-width, calc(100% - 52px));
        padding-left: 8px;
        vertical-align: middle;
        font-size: 12px;
        span {
          text-transform: uppercase;
        }
      }
    }
  }
  .bottom-header {
    border-top: 1px solid #E8E7EA;
    border-bottom: 1px solid #E8E7EA;
    .main-menu {
      position: relative;
      @include css3-sufix(display, flex);
      @include css3-prefix(justify-content, space-between);
      > li {
        display: inline-block;
        > a {
          position: relative;
          display: inline-block;
          padding: 15px 0;
          color: $primary-color;
          font-size: 14px;
          font-weight: 600;
          &::before,
          &::after {
            position: absolute;
            bottom: 0;
            right: 50%;
            height: 2px;
            width: 0;
            background: transparent;
            @include css3-prefix(transition, .4s all ease);
            content: '';
          }
          &::before {
            left: 50%;
          }
          &:hover {
            color: $base-color;
            &::before,
            &::after {
              right: 0;
              width: 50%;
              background: $base-color;
            }
            &::before {
              left: 0;
            }
          }
        }
        .subnav {
          position: absolute;
          top: 150%;
          left: 0;
          z-index: -1;
          width: 100%;
          padding: 10px;
          border-radius: 0 0 5px 5px;
          background-color: $white;
          opacity: 0;
          @include css3-prefix(box-shadow, 0 6px 10px 2px rgba($black, .07));
          @include css3-prefix(transition, .4s all ease-in);
        }
        &:hover {
          .subnav {
            top: 100%;
            z-index: 6;
            opacity: 1;
          }
        }
        .sublinks {
          > li {
            display: inline-block;
            width: 25%;
            > a {
              color: $primary-color;
              font-weight: 600;
              &:hover {
                color: $base-color;
              }
            }
            .links {
              li {
                line-height: 34px;
                a {
                  position: relative;
                  padding-bottom: 3px;
                  color: $primary-color;
                  font-size: 12px;
                  @include css3-prefix(transition, .4s all ease);
                  &::before {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    height: 1px;
                    width: 0;
                    background: transparent;
                    @include css3-prefix(transition, .4s all ease);
                    content: '';
                  }
                  &:hover {
                    color: $base-color;
                    margin-left: 10px;
                    &::before {
                      width: 100%;
                      background-color: $base-color;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .sticky-header & {
    position: fixed;
    top: -34px;
    left: 0;
    z-index: 99;
    width: 100%;
    background-color: $white;
    @include css3-prefix(box-shadow, 0 0 6px 2px rgba($black, .2));
    .bottom-header {
      display: none;
    }
  }
}
