/**
 * Components SCSS
 *
 */

/* Slider dots */
.owl-controls {
  height: 100%;
}
.owl-dots {
  position: absolute;
  top: 50%;
  right: 10px;
  left: auto;
  z-index: 99;
  display: inline-block;
  width: 25px;
  text-align: center;
  @include css3-prefix(transform, translateY(-50%));
  .owl-dot {
    display: inline-block;
    height: 15px;
    width: 15px;
    margin-bottom: 5px;
    border: 2px solid #D1D1D1;
    background-color: transparent;
    @include css3-prefix(border-radius, 100%);
    &.active {
      border-color: $base-color;
    }
  }
}
/* End Slider dots */

/* Carousel nav Design */
.owl-nav {
  position: absolute;
  top: auto;
  bottom: 0;
  display: inline-block;
  width: 100%;
  text-align: center;
  .owl-prev,
  .owl-next {
    display: inline-block;
    height: 42px;
    width: 42px;
    margin: 0 10px 30px;
    border-radius: 100%;
    background-color: #5C5C5C;
    background-image: url('../images/back.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: 20px auto;
    font-size: 0;
  }
  .owl-next {
    @include css3-prefix(transform, rotate(-180deg));
  }
}
/* Carousel nav Design */
.quickview-popup {
  .modal-dialog {
    @media (min-width: 576px) {
      max-width: 900px;
    }
  }
  .modal-body {
    @include css3-sufix(display, flex);
    @include css3-prefix(flex-wrap, wrap);
    padding: 0;
    .left-content,
    .right-content {
      width: 50%;
    }
    .left-content {
      position: relative;
      .fav {
        z-index: 9;
      }
    }
    .right-content {
      min-height: 450px;
      padding: 0 30px 30px;
      overflow-y: auto;
      border-left: solid 1px #E8E7EA;
      border-radius: 0 3px 3px 0;
      background-color: #F8F8F8;
      @include css3-sufix(display, flex);
      @include css3-prefix(flex-wrap, wrap);
      .title {
        @include css3-prefix(align-self, flex-start);
        padding-bottom: 15px;
        border-bottom: 1px solid #E8E7EA;
        h2 {
          line-height: normal;
          a {
            color: $black;
            font-size: 15px;
            font-weight: 400;
          }
        }
        .price {
          color: $base-color;
          font-size: 30px;
          font-weight: 600;
          .old-price {
            display: inline-block;
            color: #999;
            font-weight: 300;
            text-decoration: line-through;
          }
          .offer {
            display: inline-block;
            font-size: 13px;
            font-weight: 300;
          }
        }
      }
      .size,
      .quantity-wrap {
        display: inline-block;
        width: 100%;
        span {
          display: inline-block;
          width: 100%;
          margin-bottom: 5px;
          text-transform: uppercase;
        }
        select {
          display: inline-block;
          min-width: 175px;
          padding: 5px;
          border: 1px solid #E8E7EA;
          background-color: $white;
        }
      }
      .quantity-wrap {
        @include css3-sufix(display, inline-flex);
        @include css3-sufix(flex-wrap, wrap);
        @include css3-prefix(align-self, flex-end);
        .quantity {
          display: inline-block;
          width: 120px;
          input {
            width: 60px;
          }
        }
      }
      .btn-wrap {
        @include css3-sufix(display, inline-flex);
        @include css3-sufix(flex-wrap, wrap);
        @include css3-prefix(align-self, flex-end);
        @include css3-prefix(justify-content, space-between);
        width: 100%;
        a {
          width: 48%;
          height: auto;
          padding: 10px 25px;
          line-height: normal;
        }
      }
    }
  }
  .modal-header {
    position: absolute;
    top: -60px;
    right: -40px;
    border-bottom: none;
    .close {
      color: rgba($white, .6);
      font-size: 60px;
      font-weight: 200;
      &:hover {
        color: rgba($white, .6);
      }
    }
  }
}
