/**
* Form design
*/

.form-item {
  input[type='text'],
  input[type='email'],
  input[type='password'],
  input[type='number'],
  input[type='tel'],
  textarea,
  select,
  .resizable-textarea textarea,
  .form-control {
    height: 40px;
    width: 100%;
    padding: 10px;
    border: 1px solid $border-grey;
    background-color: $white;
    color: #8C969B;
    font-size: 13px;
    font-weight: 400;
    font-family: 'Rubik', sans-serif;
    @include css3-prefix(border-radius, 0);
    @include css3-prefix(box-sizing, border-box);
    @include css3-prefix(box-shadow, none);
    @include input-placeholder {
      color: #8C969B;
    }
    &:focus {
      outline: none;
      &:not([readonly]) {
        border: 1px solid $border-grey;
        background-color: $white;
        outline: none;
        @include css3-prefix(box-shadow, none);
      }
    }
  }
}
label {
  font-size: 15px;
  font-weight: 400;
  font-family: 'Rubik', sans-serif;
}
.form-item {
  select {
    padding: 5px 25px 5px 10px;
    @include css3-prefix(appearance, none);
    background-image: url('../images/arrow-down.svg');
    background-position: center right 10px;
    background-repeat: no-repeat;
    background-size: 10px auto;
  }
}
textarea {
  min-height: 110px;
  width: 100%;
}
.btn {
  position: relative;
  display: inline-block;
  height: 40px;
  min-width: 64px;
  padding: 0 16px;
  overflow: hidden;
  border: solid 1px transparent;
  border-radius: 4px;
  background: $base-color;
  color: $white;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  line-height: 38px;
  vertical-align: middle;
  outline: none;
  cursor: pointer;
  @include prefix('transition' , 'box-shadow 0.2s');
  &:hover {
    color: $white;
    box-shadow: 0 1px 2px -1px rgba(0, 0, 0, .05), 0 4px 5px 0 rgba(0, 0, 0, .05), 0 1px 10px 0 rgba(0, 0, 0, .05);
  }
}

.btn-round {
  min-width: 200px;
  padding: 7px 30px;
  background-color: $base-color;
  color: $white;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 2px;
  text-transform: uppercase;
  @include css3-prefix(border-radius, 20px);
  &:hover {
    background-color: $base-color;
    color: $white;
  }
}
button {
  &:focus {
    outline: none;
  }
}
.btn-border {
  width: auto;
  border: 1px solid $base-color;
  background-color: transparent;
  color: $base-color !important;
  &:hover {
    background-color: $base-color;
    color: $white !important;
  }
}
.btn-lg {
  min-width: 175px;
  padding: 10px 30px;
  font-size: 24px;
  font-weight: 500;
  @media (min-width: 1600px) {
    font-size: 22px;
  }
  @media (max-width: 767px) {
    padding: 6px 15px;
    font-size: 20px;
  }
}

.btn-small {
  min-width: 0;
  padding: 8px 20px;
  border-radius: 4px;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: 1px;
  @media (max-width: 767px) {
    letter-spacing: 0;
  }
}

.btn-icon {
  img {
    display: inline-block;
    width: 20px;
    margin-right: 8px;
    vertical-align: sub;
  }
}

.input-rc {
  position: relative;
  display: inline-block;
  margin: 0;
  font-size: 16px;
  vertical-align: middle;
  input[type='checkbox'] {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    + .input-rc-span {
      float: left;
      display: inline-block;
      height: 18px;
      width: 16px;
      margin: 3px 8px 0 0;
      cursor: pointer;
      &::after {
        display: inline-block;
        height: 100%;
        width: 100%;
        margin-top: -2px;
        background-image: url('../images/checkbox.png');
        background-repeat: no-repeat;
        background-size: 100% auto;
        cursor: pointer;
        content: '';
      }
    }
    &:checked {
      + .input-rc-span {
        &::after {
          background-image: url('../images/checkbox-check.png');
        }
      }
    }
  }
  ~ label {
    vertical-align: middle;
  }
}

/* Input type number custom design */
.quantity {
  position: relative;
  display: inline-block;
  width: 100px;
  .quantity-nav {
    .quantity-button {
      display: inline-block;
      height: 40px;
      width: 30px;
      border: 1px solid $border-grey;
      background-color: #F8F8F8;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
      &.quantity-up {
        float: right;
      }
    }
  }
  input {
    position: absolute;
    top: 0;
    left: 30px;
    height: 40px;
    padding: 2px 5px;
    border: 1px solid $border-grey;
    border-right: none;
    border-left: none;
    @include css3-sufix(width, calc(100% - 60px));
    text-align: center;
    &:focus {
      outline: none;
    }
    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button {
      @include css3-prefix(appearance, none);
    }
  }
}
/* End Input type number custom design */

