/**
 * Base SCSS
 */

* {
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke: 0;
  @include css3-prefix(text-shadow, none);
}

body {
  margin: 0;
  color: $base-font-color;
  font-family: 'Open Sans', sans-serif;
  @media (max-width: 767px) {
    padding-bottom: 54px;
  }
}

ul,
ol {
  margin: 0;
  padding: 0;
  li {
    list-style: none;
  }
}

a {
  text-decoration: none;
  &:hover,
  &:focus {
    text-decoration: none;
  }
}

img {
  max-width: 100%;
}

.custom-container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  @media (min-width: 576px) {
    max-width: 100%;
  }
  @media (min-width: 768px) {
    max-width: 720px;
  }
  @media (min-width: 992px) {
    max-width: 960px;
  }
  @media (min-width: 1200px) {
    max-width: 1220px;
  }
  @media (min-width: 1366px) {
    max-width: 1300px;
  }
  @media (min-width: 1600px) {
    max-width: 1350px;
  }
}

.fav {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 1;
  @include css3-sufix('display' , 'flex');
  @include css3-prefix('align-items' , 'center');
  @include css3-prefix('justify-content' , 'center');
  a {
    height: 24px;
    width: 24px;
    border: solid 1px transparent;
    border-radius: 50%;
    background-color: #C1C0C0;
    color: $white;
    font-size: 12px;
    @include css3-sufix('display' , 'flex');
    @include css3-prefix('align-items' , 'center');
    @include css3-prefix('justify-content' , 'center');
    i {
      margin-top: 2px;
    }
    &:hover {
      border-color: $base-color;
      background-color: transparent;
      color: $base-color;
    }
  }
}

.quickview {
  position: absolute;
  top: 70%;
  left: 50%;
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  cursor: pointer;
  @include css3-prefix('transform' , 'translate(-50%, -50%)');
  @media (max-width: 992px) {
    display: none;
  }
  a {
    display: block;
    padding: 5px 14px;
    border-radius: 4px;
    background: $black;
    font-size: 12px;
    font-weight: 400;
    color: $white !important;
    text-transform: uppercase;
    white-space: nowrap;
    .svg-icon {
      position: relative;
      display: inline-block;
      height: 18px;
      width: 18px;
      margin: -1px 7px 0 0;
      vertical-align: top;
    }
  }
}
